en términos de flujo de caja.")]),_c('p',{attrs:{"data-aos":"fade-right"}},[_vm._v("Sin embargo, esta búsqueda de eficiencia, también debe ser cautelosa y estratégica. Un CCE demasiado corto o basado en políticas agresivas puede convertirse en un riesgo. Por ejemplo, presionar excesivamente a los clientes para que paguen antes de tiempo puede afectar las relaciones comerciales o reducir las ventas, si los plazos no son competitivos frente al mercado. De igual manera, mantener niveles de inventario demasiado bajos para agilizar la rotación puede provocar quiebres de stock, afectando la capacidad de respuesta ante aumentos inesperados en la demanda o interrupciones en la cadena de suministro.")])])])
